<template>
    <div class="index">
      <div class="outer">
        <div class="inner">
          <ul>
            <li v-for="blog in bloglist">
              <router-link :to="{
                path:'/detail',
                query:{
                  id:blog.id
                }
                }">
                <figure>
                  <img :src="blog.user.avatar" :alt="blog.user.username">
                  <figcaption>
                    <span>{{blog.user.username}}</span>
                  </figcaption>
                </figure>
                <div class="title">
                  <h3>
                    <span>{{blog.title}}</span>
                  </h3>
                  <span>{{blog.createdAt | timefilter}}</span>
                </div>
                <p>
                  <span>{{blog.description}}</span>
                </p>
              </router-link>
            </li>
          </ul>
        </div>
      </div>
      <pager
        class="pager"
        :currentPage="currentPage"
        :totalPage="totalPage"
        :frist-page="true"
        :last-page="true"
        :pagechange="clickpage"
      ></pager>
    </div>
</template>

<script>
  import blog from "@/api/blog"
  import pager from "../components/Pager"
    export default {
      data(){
          return {
            currentPage:1,
            totalPage:"",
            bloglist:[]
          }
      },
      components:{
        pager
      },
      methods:{
        clickpage(e){
          this.currentPage=e
        }
      },
      created() {
        if(this.$route.query.page!==1&&this.$route.query.page!==undefined)this.currentPage=this.$route.query.page
        this.$router.push({path:'/',query:{page:this.currentPage}})
        blog.getIndexBlogs({page:this.$route.query.page}).then(res=>{
          this.totalPage=res.totalPage
          this.bloglist=res.data
        })
      },
      watch:{
        currentPage(newval){
          this.$router.push({path:'/',query:{page:newval}})
          blog.getIndexBlogs({page:this.$route.query.page}).then(res=>{
            this.totalPage=res.totalPage
            this.bloglist=res.data
          })
        },
      }
    }
</script>

<style scoped lang="less">
  @bgcolor:rgba(8,8,8,0.25);
  @textcolor:#DEEAED;

  .index{
    &>.outer{overflow:hidden;height: 80vh;margin-top: 2vh;
      &>.inner{height: 100%;overflow-y: auto;
        &::-webkit-scrollbar {border-radiuss: 10px;width: 5px;height: 16px;background-color: transparent;}
        &::-webkit-scrollbar-track {-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);border-radius: 10px;background-color: transparent;}
        &::-webkit-scrollbar-thumb {-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);background-color: @textcolor;}
          & li{list-style: none;overflow-x: hidden;
          & a{background: @bgcolor;display: grid;grid-template-rows: repeat(2, 1fr);grid-template-columns: 25% 75%;grid-template-areas: "a b""a c";justify-content: center;padding: 1rem 0;border-bottom:1px solid #888;border-top:1px solid #888;
            & figure {grid-area: a;
            & img{border-radius: 50%;}}
            & .title{position: relative;grid-area: b;display: flex;align-items: center;justify-content:center;
              & h3 {width:50%;display:flex;align-items: center;justify-content:center;
                & span{font-size: 2rem;width: 100%;overflow:hidden;white-space:nowrap;text-overflow: ellipsis;}}
              &>span{z-index:0;position: absolute;right: 5px;font-size: 1rem;color: rgba(255,255,255,0.5);font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;}}
            &>p {grid-area: c;overflow:hidden;text-overflow: ellipsis;display: flex;justify-content: center;align-items: center;
              & span{width: 70%;overflow:hidden;white-space:nowrap;text-overflow: ellipsis;}}
            &:hover {background: rgba(8, 8, 8, 0.5);cursor: pointer;}
          }
        }
      }
    }
    & .pager{margin: 10px 0;}
  }
  @media(max-width:500px){
    .index{
      &>.outer{height: 83vh;}
    }
  }
</style>



