<template>
   <div class="userinfo">
     <h1>用户界面</h1>
     <section class="userlist">
       <ul>
         <li><img :src="user.avatar" alt=""></li>
         <li>UserID: {{user.id}}</li>
         <li>用户名: {{user.username}}</li>
         <li>帐号创建时间: {{createdAt}}</li>
         <li>已写博客:{{total}}篇</li>
       </ul>
     </section>
     <h1>Ta的博客</h1>
     <section class="bloglist" v-if="reload">
       <div class="innerlist">
         <ul>
           <li v-for="blog in blogs" :class="{isshow:!isadmin}">
             <router-link :to="{path:'/detail',query:{id:blog.id}}" class="todetail" >
               <h5><span>{{blog.createdAt | timefilter}}</span></h5>
               <h3><span>{{blog.title}}</span></h3>
               <p><span>{{blog.description}}</span></p>
             </router-link>
             <router-link :to="{name:'edit',params:{id:blog.id,type:'edit'}}" class="edit" v-show="isadmin">
               <svg class="icon icon-style" aria-hidden="true" >
                 <use xlink:href="#icon-edit"></use>
               </svg>
             </router-link>
             <div class="delete" v-show="isadmin" @click="deleteblog(blog.id)">
               <svg class="icon icon-style" aria-hidden="true" >
                 <use xlink:href="#icon-delete"></use>
               </svg>
             </div>
           </li>
         </ul>
       </div>
     </section>
     <Pager
       class="pager"
       :currentPage="currentPage"
       :totalPage="totalpage"
       :pagechange="clickpage"
       v-show="isshow"
     ></Pager>
   </div>
</template>

<script>
  import  { mapGetters,mapActions} from "vuex"
  import blog from '@/api/blog.js'
  import Pager from "../components/Pager"
    export default {
      data(){
          return {
            user:"",
            blogs:{},
            createdAt:"",
            isadmin:false,
            isshow:false,
            currentPage:1,
            totalpage:"",
            total:0,
            reload:true
          }
      },
      components:{
        Pager
      },
      computed:{
        ...mapGetters(["vuex_islogin","vuex_user"]),
      },
      methods:{
        ...mapActions(["vuex_checklogin"]),
        clickpage(e){
          this.currentPage=e
        },
        userinfo_init(query_id){
          blog.getBlogsByUserId(query_id,{page:this.currentPage})
            .then(res=> {
              if(res.data[0].user){
                console.log(res);
                this.current=res.page;
                this.total=res.total;
                this.totalpage=res.totalPage;
                this.createdAt=res.data[0].createdAt.substring(0,10)
                this.blogs=res.data;
                this.user=res.data[0].user
                this.user=res.data[0].user
              }
            }).then(res=>{
            if(this.$route.query.id===this.vuex_user.id&&this.vuex_islogin===true){
              this.isadmin=true;
            }
          })
        },
        deleteblog(e){
          if(confirm("确定要删除吗")===true){
            blog.deleteBlog({blogId:e})
              .then((res)=>{
                this.reload=false;
                let query_id=parseInt(this.$route.query.id);
                this.userinfo_init(query_id);})
          }
        },
      },
      created() {
        this.vuex_checklogin().then(()=>{

          let query_id=parseInt(this.$route.query.id);
          if(query_id){this.userinfo_init(query_id);}
          if(query_id===this.vuex_user.id&&this.vuex_islogin===true){
            this.isadmin=true;
            this.user=this.vuex_user
            this.createdAt=this.vuex_user.createdAt.substring(0,10)
          }
        })
      },
      watch:{
        $route(newval){
          this.userinfo_init(newval.query.id);
          if(this.$route.query.id===this.vuex_user.id&&this.vuex_islogin===true){
            this.isadmin=true;
            this.user=this.vuex_user
            this.createdAt=this.vuex_user.createdAt.substring(0,10)
          }
        },
        currentPage(newval){
          let query_id=this.$route.query.id;
          blog.getBlogsByUserId(query_id,{page:newval})
            .then(res=>this.blogs=res.data)
        },
        total(newval){
          if(newval>10){this.isshow=true}},
        reload(newval,oldval){
          if(newval===false){
            setTimeout(()=>{this.reload=true},500)}
          }
      }
  }
  </script>

  <style scoped lang="less">
  @bgcolor:rgba(8,8,8,0.25);
  @textcolor:#DEEAED;
  @hovercolor: rgba(8, 8, 8, 0.5);

  .userinfo{max-width: 100%;
    &>h1{margin: 1.5rem 0;}
    &>.userlist{
      &>ul{height: 6rem;display: grid;grid-template-areas: "a b""a c""a d""a e";background: @bgcolor;padding: 1rem 0;text-align: left;
        & li{list-style: none;padding-left: 2rem;}
        & li:nth-child(1){grid-area: a;text-align: center;}
        & li:nth-child(2){grid-area: b;}
        & li:nth-child(3){grid-area: c;}
        & li:nth-child(4){grid-area: d;}
        & li:nth-child(5){grid-area: e;}
      }
    }
    &>.bloglist{overflow:hidden;margin-top: 1vh;transition: all 1s linear;height: 58%;
      &>.innerlist{height: 100%;width: 100%;overflow-y: auto;
        &::-webkit-scrollbar {border-radius: 10px;width: 5px;height: 16px;background-color: transparent;}
        &::-webkit-scrollbar-track {-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);border-radius: 10px;background-color: transparent;}
        &::-webkit-scrollbar-thumb {-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);background-color: @textcolor;}
        & li{list-style: none;display: grid;grid-template-areas: "a a b""a a c";grid-template-rows: repeat(2, 3rem);grid-template-columns:10% 85% 5%;justify-items:center;justify-content: center;border-bottom:1px solid #888;
          &:first-child{border-top:1px solid #888;}
          & .todetail{width: 100%;display: grid;grid-template-areas: "a b""a c";justify-content: center;align-items:center;grid-template-columns:15% 85%;background: rgba(8, 8, 8, 0.25);grid-area: a;
            & h5{grid-area: a;display: flex;justify-content: center;align-items: center;
              & span{color: rgba(255,255,255,0.5);font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;}}
            & h3 {padding: 0 2rem;grid-area: b;font-size: 2rem;overflow:hidden;white-space:nowrap;text-overflow: ellipsis;
              & span{width: 70%}}
            & p {grid-area: c;overflow:hidden;white-space:nowrap;text-overflow: ellipsis;
              & span{width: 70%}}
            &:hover {background:@hovercolor;cursor: pointer;} }
          & .edit,.delete{grid-area: b;display: flex;background: @bgcolor;width: 100%;height: 100%;justify-content: center;align-items: center;
            &:hover {background: @hovercolor;cursor: pointer;} }
          & .delete{grid-area: c;}
        }
        & li.isshow{grid-template-areas: "a a a""a a a";}
        }
      }
    &>.pager{margin-top: 2rem;}
  }
  .icon {width: 50%;height: 50%;vertical-align: -0.15em;fill: currentColor;overflow: hidden;}
</style>



