<template>
    <div class="edit">
      <section class="title">
        <p>title: <span>博客标题不能为空，且不超过{{titleNum}}个字符</span></p>
        <textarea maxlength="100"  class="title_area" v-model="title"></textarea>
      </section>
      <section>
        <p>description:<span>简要描述,可为空，如果为空则自动从content 中提取</span></p>
        <span></span>
        <textarea  maxlength="100" class="description_area" v-model="description"></textarea>
      </section>
      <section>
        <p>content:<span>博客内容不能为空，且不超过{{contentNum}}个字符</span></p>
        <textarea maxlength="10000"  class="content_area" v-model="content"></textarea>
      </section>
      <section class="commit">
        <div>
          是否展示在首页
          <input type="checkbox" v-model="atIndex"/>
        </div>
        <div class="button" @click="commitclick">{{commit}}</div>
      </section>
    </div>
</template>

<script>
  import blog from "@/api/blog"
    export default {
        name: "edit",
      data(){
        return{
          blogId:this.$route.params.id,
          type:this.$route.params.type,
          atIndex:false,
          title:"",
          titleNum:100,
          description:"",
          content:"",
          contentNum:10000
        }
      },
      computed:{
          commit(){
            if(this.type==="creat"){
              return "创建博客"
            }else if(this.type==="edit"){
              return "变更博客"
            }
          }
      },
      methods:{
        titlenumber(e){
          if(e==="")e=0
          this.titleNum=100-parseInt(e)
        },
        contentnumber(e){
          if(e==="")e=0
          this.contentNum=10000-parseInt(e)
        },
        commitclick(){
          if(this.type==="creat"){
           blog.createBlog({
           title:this.title,
             description:this.description,
             content:this.content,
             atIndex:this.atIndex}).then(res=>{
             console.log(res);
             this.$router.push({path:'detail',query:{id:res.data.id}})
           })
          }else if(this.type==="edit"){
            blog.updateBlog(
              {blogId:this.blogId},
              {title:this.title,
              description:this.description,
              content:this.content,
              atIndex:this.atIndex}).then(res=>{
              console.log(res);
              this.$router.push({path:'detail',query:{id:res.data.id}})
            })
          }
        }
      },
      created(){
        if(this.blogId){
          blog.getDetail({blogId:this.blogId}).then(res=>{
            let R=res.data;
            this.title=R.title;
            this.description=R.description;
            this.content=R.content;
            this.atIndex=R.atIndex;
          })
        }
      },
      watch:{
        title(newval){
          this.titlenumber(newval.length);
        },
        content(newval){
          this.contentnumber(newval.length);
        }
      }
    }
</script>

<style scoped lang="less">
  .edit{
    & section{text-align: left;margin-top: 1rem;width: 100%;}
    & p{font-size: 2rem;margin-bottom: 0.5rem;
      & span{font-size: 1rem;color: rgba(255,255,255,0.75);float: right;margin-top: 0.5rem;}}
    & textarea{box-sizing: border-box;background:rgba(8,8,8,0.25);width: 100%;padding: 1.5rem;resize: none;font-size: 1.5rem;font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;caret-shape: auto; outline: none;border: 1px solid transparent;transition: all 0.30s ease-in-out;margin-top: 0.5rem;
      &:focus{transition: all 0.30s ease-in-out; border:1px solid rgba(255,255,255,0.25);}
    }
    & .title_area{min-height:5rem;}
    & .description_area{min-height: 10vh;}
    & .content_area{min-height: 38vh;}
    & .commit{text-align: center;
      & .button{background: #000;padding: 0.5rem 1rem;width: 5rem;display: inline-block;cursor: pointer;user-select:none;margin-top: 0.5rem;}
    }
  }
  @media (max-width:1000px ) {
    .edit{
      & .title_area{min-height:5rem;}
      & .description_area{min-height: 10vh;}
      & .content_area{min-height: 35vh;}
    }
  }
</style>



