<template>
    <div class="detail">
      <section class="title_area">
        <span>{{title}}</span>
      </section>
      <ul class="clearfloat">
        <li>
          <span>作者 :</span>
          <router-link :to="{path:'userinfo',query:{id:user.id}}">{{user.username}}</router-link>
        </li>
        <li>
          <span>发布于 :</span>
          <span>{{createdtime}}</span>
        </li>
        <li>
          <span>最后更新于:</span>
          <span>{{updated| timefilter}}</span>
        </li>
      </ul>
      <section class="description_area">
        <span>{{description}}</span>
      </section>
      <section  class="content_area outer">
        <div class="inner" v-html="markdown"></div>
      </section>
    </div>
</template>

<script>
  import blog from "../api/blog"
  import marked from "marked"
  window.blog=blog;
    export default {
        name: "detail",
      data(){return {
            blogid:"",
            title:"",
            description:"",
            content:"",
            created:"",
            updated:"",
            user:{},
          }
      },
      computed:{
          createdtime(){
            let year=this.created.substring(0,4);
            let month=this.created.substring(5,7);
            let day=this.created.substring(8,10);
            let date=year+"年"+month+"月"+day+"日";
            return date
          },
          markdown(){
            return marked(this.content)
          }
        },
      methods:{
          getblog(){
            blog.getDetail({blogId:this.blogid}).then(res=>{
              let R=res.data;
              this.title=R.title;
              this.description=R.description;
              this.content=R.content;
              this.user=R.user;
              this.created=R.createdAt;
              this.updated=R.updatedAt;
            })
        },
        check(){
           blog.getBlogsByUserId(this.user.id,1).then(res=>console.log(res))}
        },
      created(){
        this.blogid=this.$route.query.id;
        this.getblog()
      },

    }
</script>

<style scoped lang="less">
  @bgcolor:rgba(8,8,8,0.25);
  @textcolor:#DEEAED;
*{margin: 0;padding: 0;box-sizing: border-box;}

  .detail{text-align: left;
    & ul{border-bottom: 1px solid @textcolor;
      & li{list-style: none;margin-left: 2rem;float: left;
        &:first-child{margin-left: 0;}
        &:last-child{margin-left: 0;float: right;}
        & span:first-child{font-size: 1.5rem;}
        & span:last-child{color: @textcolor;}
        & a{text-decoration: underline;color: @textcolor;}
      }}
    & section{background: @bgcolor;padding:1.5rem;}
    & .title_area{word-wrap: break-word;margin: 1rem 0;background: @bgcolor;text-align: center;}
    & .description_area{margin: 1rem 0 ;min-height: 5vh;
      &>span{word-wrap: break-word;}}
    & .content_area{overflow:hidden;height: 50vh;
      & .inner{overflow:scroll;height: 50vh;box-sizing: border-box;overflow-x:hidden;word-wrap: break-word;
        &::-webkit-scrollbar{border-radius: 10px;width: 5px;height: 16px;background-color: transparent;}
        &::-webkit-scrollbar-track{-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);border-radius: 10px;background-color: transparent;}
        &::-webkit-scrollbar-thumb{-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);background-color: @textcolor;}
      }
    }
  }

@media (max-width: 1000px) {
  .detail{
    & .content_area{overflow:hidden;min-height: 65%;
      & .inner{max-height: 100%;  }
      }}
}

</style>



