import auth from '@/api/auth'

export default{
  state:{
    vuex_user:{},
    vuex_islogin:false
  },
  getters:{
    vuex_user: state => state.vuex_user,
    vuex_islogin: state => state.vuex_islogin
  },
  mutations:{
    setuser(state, newvalue){
      state.vuex_user=newvalue.vuex_user
    },
    setislogin(state,newvalue){
      state.vuex_islogin=newvalue.vuex_islogin
    }
  },
  actions:{
    async vuex_login({commit},{username,password}){
       let res=await auth.login({username,password})
      commit('setuser', { vuex_user: res.data })
      commit('setislogin', { vuex_islogin: true })
      return res.data
    },
    async vuex_register({commit},{username,password}){
      let res=await auth.register({username,password})
      console.log(res);
      commit('setuser', { vuex_user:{
          avatar:res.data.avatar,
          id:res.data.id,
          username:res.data.username,
          createdAt:res.data.createdAt
        }})
      commit('setislogin', { vuex_islogin: true })
      return res.data
    },
    async vuex_checklogin({commit,state}){
      let res =await auth.getInfo()
      if(res.isLogin===true){
        commit('setislogin', { vuex_islogin: true })
        commit('setuser', { vuex_user: {
            avatar:res.data.avatar,
            id:res.data.id,
            username:res.data.username
          }})}
      if(res.isLogin===false){
        commit('setislogin', { vuex_islogin: false })
      }
    },
    async vuex_logout({ commit ,state}) {
      await auth.loginout()
      commit('setuser', { vuex_user: null })
      commit('setislogin', { vuex_islogin: false })
    }
  }
}



