<template>
  <div id="app">
    <Header id="header"/>
    <router-view id="main"/>
    <Footer id="footer"/>
  </div>
</template>


<script>
  import Header from './components/header.vue'
  import Footer from './components/footer.vue'

export default {
  name: 'App',
  components:{
    Header,
    Footer
  }
}
</script>

<style>
  html{font-size: 10px;height: 100%;}
  a{text-decoration: none;}
  *{margin: 0;padding: 0;color: white;}
  body{height: 100%;}
  #app {
    min-height: 100%;
    text-align: center;
    background: #3E7190;
    display: grid;
    grid-template-areas:'header header header'
                        '.     main   .      '
                        'footer footer footer';
    grid-template-rows: 50px auto 40px;
    grid-template-columns: 15% 70% 15%;
    transition: all 1s;
  }
  @media (max-width:400px) {
    #app{grid-template-columns: 10% 80% 10%; grid-template-rows: 6% 90% 4%;}

  }
  @media screen and (min-width:1000px)and (max-width:1200px) {
    #app{grid-template-columns: 25% 50% 25%;}
  }
  @media (min-width: 1200px) {
    #app{grid-template-columns: 30% 40% 30%;}
  }
  #header {
    grid-area: header;
    padding: 0 12%;
  }
  #main{
    grid-area: main;
  }
  #footer {
    grid-area: footer;

  }
  .clearfloat:after{
    content: "";
    display: block;
    clear: both;
  }


  


</style>





