<template>
  <div class="login">
    <section>
      <h1>登录</h1>
    </section>
    <section>
      <input class="el" v-model="username"  placeholder="请输入用户名"></input>
    </section>
    <section>
      <input
        class="el"
        v-model="password"
        placeholder="请输入密码"
        type="password"
      @keyup.enter="login">
      </input>
    </section>
    <section v-if="error">
      <svg class="icon icon-style" aria-hidden="true" >
        <use xlink:href="#icon-error"></use>
      </svg>
      <label>{{error_text}}</label>
    </section>
    <section>
      <button @click="login">登录</button>
    </section>
  </div>
</template>

<script>
  import { mapActions,mapMutations } from 'vuex'

  export default {
    name: "registered",
    data(){
      return {
        error_text:'两次密码不一致',
        username:'',
        password:'',
        error:false
      }
    },
    methods: {
      ...mapActions(['vuex_login']),
      ...mapMutations(['setuser']),
      login() {
        if (this.username === "") {
          this.error=true;
          this.error_text='用户名不能为空'
        }else if(this.password === ""){
          this.error=true;
          this.error_text='请输入密码'
        }else{
          this.vuex_login({
            username:this.username,
            password:this.password
          })
            .then(res=>{
            this.setuser({vuex_user:res})
            this.$router.push({path:'/userinfo',query:{id:res.id}});
          }).catch(res=>{
            this.error_text=res.msg;
            this.error=true
          })
        }
      }
    }
  }
</script>

<style scoped lang="less">
  .login{height: 60rem;
    section:first-child{margin-top: 7rem;}
    section{margin: 3rem;
      &:after{content: '';clear: both;display: block;}
      input{width: 50%;border:1px solid transparent;border-bottom:1px solid white;font-size: 1.5rem;padding-bottom: 0.5rem;background: transparent;text-align: center;}
      input::placeholder{color: #A0A0A0;}
      input:focus{outline: none;}
      button{padding:  0.5rem 1.5rem;
        background:#464646;border:1px solid white;border-radius: 5px;cursor: pointer;}
      button:hover{background:black;color: white;}
      label{font-size: 1.5rem;}
      svg{height:1.5rem;}
    }
  }
  .icon-style{width: 2rem;}

</style>



