import Vue from 'vue'
import Router from 'vue-router'
import index from '../pages/index'
import edit from '../pages/edit'
import login from '../pages/login'
import reg from '../pages/registered'
import userinfo from '../pages/userinfo'
import detail from '../pages/detail'



Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: index
    },
    {
      path: '/edit',
      name:'edit',
      component: edit
    },
    {
      path: '/login',
      component: login
    },
    {
      path: '/registered',
      name:'reg',
      component: reg
    },
    {
      path: '/userinfo',
      name:'userinfo',
      component: userinfo
    },
    {
      path: '/detail',
      name:'detail',
      component: detail
    }

  ]
})



