<template>
  <div class="registered">
     <section>
       <h1>注册</h1>
     </section>
    <section>
      <label>
        <input class="el" v-model="username"  placeholder="请输入用户名"></input>
      </label>
    </section>
    <section>
      <label>
        <input
          class="el"
          v-model="password"
          placeholder="请输入密码"
          @blur="check_password"
          type="password">
        </input>
      </label>
    </section>
    <section>
      <label>
        <input
          class="el"
          v-model="re_password"
          placeholder="再次确认密码"
          @blur="check_password"
          type="password"
          >
        </input>
      </label>
    </section>
    <section v-if="check">
        <svg class="icon icon-style" aria-hidden="true" >
          <use xlink:href="#icon-error"></use>
        </svg>
        <label>{{error_text}}</label>
    </section>
    <section>
      <button @click="register">注册</button>
      <button @click="reset">重置</button>
    </section>
  </div>
</template>

<script>
import '../api/auth'
import { mapActions } from 'vuex'

    export default {
        name: "registered",
      data(){
          return {
            error_text:'两次密码不一致',
            username:'',
            password:'',
            re_password:'',
            error:'',
            check:''
          }
      },
      methods: {

        ...mapActions(['vuex_register']),

        check_password() {
          this.check = this.re_password !== this.password;
        },
        register() {
          if (this.username === "") {
            this.check=true;
            this.error_text='用户名不能为空'
          }else if(this.password === ""){
            this.check=true;
            this.error_text='请输入密码'
          }else if(this.error===true){
            this.error_text='两次密码不一致'
          }else{
            this.vuex_register({username:this.username, password:this.password})
              .then(res=>{
                console.log(res);
                this.$router.push({path:'/userinfo',query:{id:res.id}});

              })
              .catch(res=>{
                this.error_text=res.msg;
                this.check=true
            })}
          },
        reset(){
          this.username="";
          this.password="";
          this.re_password="";
        }
        }
      }
</script>

<style scoped lang="less">
  .registered{height: 60rem;
    section:first-child{margin-top: 7rem;}
    section{margin: 3rem;
      &:after{content: '';clear: both;display: block;}
      input{width: 50%;border:1px solid transparent;border-bottom:1px solid white;font-size: 1.5rem;padding-bottom: 0.5rem;text-align: center;background: transparent;}
      input::placeholder{color: #A0A0A0;}
      input:focus{outline: none;}
      button{outline:none;padding:  0.5rem 1.5rem;background:#464646;border:1px solid white;border-radius: 5px;cursor: pointer;}
      button:hover{background:black;color: white;}
      label{font-size: 1.5rem;}
      svg{height:1.5rem;}
    }
  }
  .icon-style{width: 2rem;}
</style>



