<template>
    <div id="header">
        <router-link class="logo" :to="{path:'/',query:{page:1}}">Let`s share</router-link>
        <div class="user">
          <router-link :to="{name:'edit',params:{type:'creat'}}" >
            <svg class="icon icon-style" aria-hidden="true">
              <use xlink:href ="#icon-edit"></use>
            </svg>
          </router-link>
          <span class="settings" @click="myshow">
            <svg class="icon icon-style" aria-hidden="true" >
              <use xlink:href="#icon-settings"></use>
            </svg>
            <ul :class="{'showUL':noShow}" v-if="vuex_islogin">
              <li>
                 <router-link :to="{
                  path:'userinfo',
                  query:{id:vuex_user.id}
                  }">
                 <img :src="vuex_user.avatar" alt="">
                 <span>{{vuex_user.username}}</span>
               </router-link>
              </li>
              <li @click="logout">
                 <svg class="icon icon-style" aria-hidden="true" >
                   <use xlink:href="#icon-logout"></use>
                 </svg>
                <span>登出</span>
              </li>
            </ul>
            <ul :class="{'showUL':noShow}" v-else @mouseleave="noShow" >
              <li>
               <router-link to="/login">
                 <svg class="icon icon-style" aria-hidden="true" >
                   <use xlink:href="#icon-my"></use>
                 </svg>
                 <span>登录</span>
               </router-link>
              </li>
              <li>
               <router-link to="/registered">
                 <svg class="icon icon-style" aria-hidden="true" >
                   <use xlink:href="#icon-registered"></use>
                 </svg>
               </router-link>
                <span>注册</span>
              </li>
            </ul>
          </span>
        </div>
    </div>
</template>

<script>
  import '../assets/iconfont.js'
  import { mapGetters, mapActions} from 'vuex'
    export default {
      data(){
        return {
          noShow:true
        }
      },
      computed:{
        ...mapGetters(['vuex_islogin','vuex_user'])
      },
      methods:{
        ...mapActions(['vuex_logout','vuex_checklogin']),
        myshow(){
            this.noShow=!this.noShow;
            },
        logout(){
          this.vuex_logout();
          window.location.reload()
        }
      },
      created() {
        this.vuex_checklogin();
      }
    }
</script>


<style scoped lang="less">
  @iconSize:3rem;
  a:visited{color: white;}
  @keyframes show {
      0%{opacity: 0}
      100%{opacity: 1}
  }
  #header {background: black;color: white;display:flex;justify-content: space-between;align-items: center;
  & .logo {font-size: 3.2rem;user-select: none;}
    & .user {float: right;user-select:none;
        & .icon-style {width:@iconSize;height:@iconSize;}
        & .settings{cursor: pointer;position: relative;display: inline-block;
            & .showUL{display:none;animation: ease-in-out show 0.2s;}
            & ul{animation: ease-in-out show 0.2s;z-index:2;position: absolute;left:-3rem;width: 10rem;height: auto;background: black;border-radius: 5%;box-shadow:0 0 10px rgba(0,0,0,0.25);
              & li:first-child{border-radius:   5% 5% 0 0;}
              & li:last-child{border-bottom:1px solid transparent;border-radius:  0 0 5% 5%;}
              & li:hover{background: #888;}
              & li{list-style: none;height: auto;border-bottom: white 1px solid;
                 & svg{margin: 1rem;margin-bottom: 0.5rem;width: 7rem;}
                 & span{display: block;margin-bottom: 0.5rem;}
          }
        }
      }
    }
    .icon {width: 1em;height: 1em;vertical-align: -0.15em;fill: currentColor;overflow: hidden;}
  }
</style>



