<template>
  <div class="pager">
    <div @click="Previous"  :class="{stop:current===1}" >上一页</div>
    <ul  @click="pagecilck" class="clearfloat pagelist">
      <li  v-for="(item,index) in pages" :class="{currentpage:item===current}">{{item}}</li>
    </ul>
    <div @click="next" :class="{stop:current===totalpage}">下一页</div>
  </div>
</template>

<script>
  export default {
    name: "Pager",
    data(){
    return {
        current:this.currentPage,
        totalpage:"",
        pager:1
      }
    },
    props:{
      currentPage : "",
      totalPage:"",
      pagechange:""
    },
    computed: {
      pages() {
        const c = parseInt(this.current)
        const t = parseInt(this.totalPage)
        if (c < 4) {
          if(t<4){
            let arr=[]
            for(let i=1;i<=t;i++){arr[i-1]=i}
            return arr
          }else{
            return [1,2,3,'...',t]
          }
        } else if (c >= t - 2) {
          return [1,'...',t-2, t-1, t]
        } else {
          return [ 1,'...',c,'...',t]
        }
      }
    },
    methods:{
      pagecilck(e){
        if(!(e.target.innerHTML==="...")){
          this.pagechange(e.target.innerHTML)
        }
      },
      fristpage(){
        this.pagechange(1)
      },
      lastpage(){
        this.pagechange(this.totalpage)
      },
      Previous(e){
        let num=parseInt(this.current)
        if(num>1){
          this.pagechange(parseInt(this.current)-1)
        }
      },
      next(e){
        let num=parseInt(this.current)
        if(num<this.totalPage){
        this.pagechange(parseInt(this.current)+1)
        }
      }
    },
    beforeUpdate(){
    },
    watch:{
      totalPage(newval){
        newval<5?this.zuida=newval:this.zuida=5
        this.totalpage=newval
      },
      currentPage(newval){
        this.current=parseInt(newval)}
    }
}
</script>

<style lang="less">
  .pager{text-align: center;width: 100%;display: flex;flex-direction: row;justify-content:center;
    & .stop{cursor: not-allowed;background: rgba(255,255,255,0.2);}
    &>div{user-select:none;background: #000;cursor: pointer;list-style: none;padding: 0.5rem 1.2rem ;background: rgba(8, 8, 8, 0.25);
      &:hover{ background: rgba(8, 8, 8, 0.5);}
    }
    &>.pagelist{display: flex;flex-direction: row;justify-content:center;
      &>li{user-select:none;width: 1.5rem;background: #000;cursor: pointer;list-style: none;padding: 0.5rem 1.2rem ;background: rgba(8, 8, 8, 0.25);
        &:hover{ background: rgba(8, 8, 8, 0.5);} }
      &>.currentpage{background: rgba(8, 8, 8, 0.5);}
    }
  }
  @media (max-width: 1000px) {
    .pager{
      &>div{padding:  0.5rem;}
      &>.pagelist{
        &>li{padding: 0.5rem;}
      }
    }
    @media (max-width: 400px) {
      .pager{
        &>div{padding: 0 0.2rem;}
        &>.pagelist{
          &>li{padding: 0 0.2rem;}
        }
      }
    }
  }
</style>



