import blog from '@/api/blog'

export default{
  state:{
    title:"",
    description:"",
    content:""
  },
  getter:{
    vuex_title: state => state.title,
    vuex_description: state => state.description,
    vuex_content: state => state.content
  },
  mutations:{
    settitle(state, newvalue){
      state.vuex_user=newvalue.vuex_user
    },
  },
  actions:{

  }
}



